.btn-close {
  width: 16px;
  height: 16px;
  position: relative;
  &::before,
  &::after {
    content: '';
    position: absolute;
    width: 13px;
    height: 1.33px;
    border-radius: 2px;
    background-color: var(--gray-dark);
    top: calc(50% - 0.2rem);
    left: calc(50% - .8rem);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
